import CityCard from '../../components/cityCard';
import {Link} from 'react-router-dom';
import {useState} from "react";
import Button from 'react-bootstrap/Button';
import style from "./style.module.scss";
import Watch from "../../components/Watch";
import Modal from 'react-bootstrap/Modal';
import "./style.css";


function Main() {
  const [showModal, setShowModal] = useState();
  const cityList = []
  for (const key in localStorage) {
    if (Object.hasOwnProperty.call(localStorage, key)) {
      const element = localStorage[key];
      cityList.push(element)
    }
  }

  return (
    <div className={style.container}>
        <Watch />
        <div onClick={() => {
          setShowModal(true);
        }} className={style.setting_btn}> <svg t="1643515242682" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2077" width="32" height="32"><path d="M550.4 74.666667c25.6 0 46.933333 19.2 53.333333 44.8l14.933334 85.333333 38.4 17.066667L727.466667 170.666667c19.2-14.933333 46.933333-12.8 66.133333 4.266666l2.133333 2.133334 53.333334 53.333333c19.2 19.2 21.333333 46.933333 6.4 68.266667l-49.066667 70.4 17.066667 38.4 85.333333 14.933333c23.466667 4.266667 42.666667 25.6 44.8 49.066667v78.933333c0 25.6-19.2 46.933333-44.8 53.333333l-85.333333 14.933334-17.066667 38.4 49.066667 70.4c14.933333 19.2 12.8 46.933333-4.266667 66.133333l-2.133333 2.133333-53.333334 53.333334c-19.2 19.2-46.933333 21.333333-68.266666 6.4l-70.4-49.066667-38.4 17.066667-14.933334 85.333333c-4.266667 23.466667-25.6 42.666667-49.066666 44.8h-78.933334c-25.6 0-46.933333-19.2-53.333333-44.8l-14.933333-85.333333-38.4-17.066667-72.533334 46.933333c-19.2 14.933333-46.933333 12.8-66.133333-4.266666l-2.133333-2.133334-53.333334-53.333333c-19.2-19.2-21.333333-46.933333-6.4-68.266667l49.066667-70.4-17.066667-38.4-85.333333-14.933333c-23.466667-4.266667-42.666667-25.6-44.8-49.066667v-78.933333c0-25.6 19.2-46.933333 44.8-53.333333l85.333333-14.933334 17.066667-38.4L170.666667 296.533333c-14.933333-19.2-12.8-46.933333 2.133333-64l2.133333-2.133333 53.333334-53.333333c19.2-19.2 46.933333-21.333333 68.266666-6.4l70.4 49.066666 38.4-17.066666 14.933334-85.333334c4.266667-23.466667 25.6-42.666667 49.066666-44.8H550.4z m-38.4 320c-64 0-117.333333 53.333333-117.333333 117.333333s53.333333 117.333333 117.333333 117.333333 117.333333-53.333333 117.333333-117.333333-53.333333-117.333333-117.333333-117.333333z" p-id="2078" fill="#ffffff"></path></svg>
        </div>
        <Modal 
        size="lg"
        show={showModal}
        onHide={() => setShowModal(false)}
        aria-labelledby="example-modal-sizes-title-sm">
            <Modal.Header closeButton>
                <Modal.Title id="example-modal-sizes-title-sm">
                  Settings
                </Modal.Title>
            </Modal.Header>
            <Modal.Body >
            
            
              <div id="btn_group">
                <div>
                  <Link to="/SearchBar">
                    <Button variant="success" id="add_btn"><h1>Add City</h1></Button>{' '}
                  </Link>
                </div>
                <div >
                  <Link to="/RemoveSection">
                    <Button variant="danger" id="remove_btn"><h1>Remove City</h1></Button>{' '}
                  </Link>
                </div>
              </div>
           
                
            </Modal.Body>
        </Modal>
        <hr className={style.divider}/>
        {cityList.map((cityname) => {
          return <CityCard key={cityname} cityName={cityname} />;
        })}
      </div>
  );
}

export default Main;